@import 'null.scss';
$fontColor: #566885;
a,
a:visited {
	color: inherit;
	text-decoration: none;
}
body {
	color: $fontColor;
	overflow-x: hidden;
	background-color: #e9e6e6;
	font-family: 'Roboto' !important;
}

.forms {
	display: flex;
	align-items: center;
	justify-content: center;
	height: calc(100vh - 50px);
}

@import 'fonts';